<template>
    <div class="tab-content">
        <ul class="block">
            <li>
                <span class="demonstration">请选择组织架构</span>
                <el-cascader :options="organizational" change-on-select size="small" @change="structure" style="width: 500px"></el-cascader>
            </li>
            <li>
                <span class="demonstration">时间</span>
                <el-date-picker v-model="times" type="datetimerange" placeholder="选择时间范围" size="small"></el-date-picker>
            </li>
            <li>
                <span class="demonstration">彩之云订单号</span>
                <el-input
                        placeholder="请输入彩之云订单号"
                        v-model="CzyID" style="width:240px;" size="small">
                </el-input>
            </li>
            <li>
                <span class="demonstration">手机号码</span>
                <el-input
                        placeholder="请输入手机号码"
                        v-model="Mobile" style="width:240px;" size="small">
                </el-input>
            </li>
            <li>
                <span class="demonstration">商户名称</span>
                <!--<el-select v-model="MerchantName" placeholder="请搜索选择商户名称">-->
                    <!--<el-option v-for="item in SH_NAME" :key="item.value" :label="item.label" :value="item.value">-->
                    <!--</el-option>-->
                <!--</el-select>-->
                <el-select
                        v-model="MerchantName"
                        :multiple="false"
                        filterable
                        remote
                        reserve-keyword
                        placeholder="请输入商户关键词"
                        :remote-method="remoteMethod"
                        :focus="GetFocus"
                        :loading="loading"
                        size="small">
                    <el-option
                            v-for="item in options"
                            :key="item.uuid"
                            :label="item.name"
                            :value="item.name">
                    </el-option>
                </el-select>
            </li>
            <!--<li>-->
                <!--<span class="demonstration">应用名称</span>-->
                <!--<el-select v-model="applicationName" placeholder="请搜索选择应用名称">-->
                    <!--<el-option v-for="item in YY_NAME" :key="item.value" :label="item.label" :value="item.value">-->
                    <!--</el-option>-->
                <!--</el-select>-->
            <!--</li>-->
            <li>
                <span class="demonstration">订单状态</span>
                <el-select v-model="Order_status" placeholder="请选择订单状态" size="small">
                    <el-option v-for="item in Order" :key="item.id" :label="item.label" :value="item.id">
                    </el-option>
                </el-select>
            </li>
            <li>
                <el-button type="primary" icon="search" @click="Order_Search" size="small"></el-button>
            </li>
        </ul>
        <div class="box_table">
            <el-table :data="OrderTableData" v-loading="loadListing" border style="width: 100%">
                <el-table-column prop="shop_name" label="商户名称" width="160">
                </el-table-column>
                <el-table-column prop="colour_sn" label="彩之云订单号" width="240">
                </el-table-column>
                <el-table-column prop="e1_name" label="小区" width="100">
                </el-table-column>
                <el-table-column prop="e2_name" label="事业部" width="100">
                </el-table-column>
                <el-table-column prop="e3_name" label="大区" width="100">
                </el-table-column>
                <el-table-column prop="real_total_fee" label="支付金额" width="100">
                </el-table-column>
                <el-table-column prop="payment_name" label="支付方式">
                </el-table-column>
                <el-table-column label="订单状态">
                    <template scope="scope">
                        <el-tag type="primary" v-if="scope.row.trade_state==1">未支付</el-tag>
                        <el-tag type="primary" v-if="scope.row.trade_state==2">已付款</el-tag>
                        <el-tag type="primary" v-if="scope.row.trade_state==3">交易成功</el-tag>
                        <el-tag type="primary" v-if="scope.row.trade_state==4">已关闭</el-tag>
                        <el-tag type="primary" v-if="scope.row.trade_state==5">已撤销</el-tag>
                        <el-tag type="primary" v-if="scope.row.trade_state==6">用户支付中</el-tag>
                        <el-tag type="primary" v-if="scope.row.trade_state==7">转入退款</el-tag>
                        <el-tag type="primary" v-if="scope.row.trade_state==8">支付失败</el-tag>
                    </template>
                </el-table-column>
                <el-table-column prop="mobile" label="手机号码">
                </el-table-column>
                <el-table-column label="创单时间">
                    <template scope="scope">
                        <span>{{scope.row.time_pay | time}}</span>
                        <!--二者选一都可以实现-->
                        <!--<span>{{new Date(scope.row.time_pay).toLocaleString()}}</span>-->
                    </template>
                </el-table-column>
                <el-table-column label="操作">
                    <template scope="scope">
                        <el-button @click="details_Click(scope.$index, scope.row)" type="text" size="small">查看</el-button>
                        <el-button type="text" size="small" @click="NoticeWithdrawal(scope.$index, scope.row)">通知</el-button>
                    </template>
                </el-table-column>
            </el-table>
        </div>
        <el-dialog title="订单详情" v-model="details_Visible" :close-on-click-modal="false">
            <ul class="OneOrder">
                <li>
                    商户名称：{{OrderTableDataOne.shop_name}}
                </li>
                <!--<li>-->
                    <!--应用名称：{{OrderTableDataOne.general_name}}-->
                <!--</li>-->
                <li>
                    彩之云订单号：{{OrderTableDataOne.colour_sn}}
                </li>
                <li>
                    商户订单号：{{OrderTableDataOne.out_trade_no}}
                </li>
                <li>
                    用户标识：{{OrderTableDataOne.open_id}}
                </li>
                <li>
                    手机号码：{{OrderTableDataOne.mobile}}
                </li>
                <li>
                    订单金额：{{OrderTableDataOne.meal_total_fee}}
                </li>
                <li>
                    支付金额：{{OrderTableDataOne.total_fee}}
                </li>
                <li>
                    支付方式：{{OrderTableDataOne.payment_uuid}}
                </li>
                <li>
                    支付折扣率：{{OrderTableDataOne.discount}}
                </li>
                <li>
                    商品描述：{{OrderTableDataOne.body}}
                </li>
                <li>
                    商品详情：{{OrderTableDataOne.detail}}
                </li>
                <li>终端IP:{{OrderTableDataOne.spbill_create_ip}}</li>
                <li>交易开始时间：{{OrderTableDataOne.time_start | time}}</li>
                <li>交易结束时间：{{OrderTableDataOne.time_expire | time}}</li>
                <li>
                    订单状态：
                    <el-tag type="primary" v-if="OrderTableDataOne.trade_state==1">未支付</el-tag>
                    <el-tag type="primary" v-if="OrderTableDataOne.trade_state==2">已付款</el-tag>
                    <el-tag type="primary" v-if="OrderTableDataOne.trade_state==3">交易成功</el-tag>
                    <el-tag type="primary" v-if="OrderTableDataOne.trade_state==4">已关闭</el-tag>
                    <el-tag type="primary" v-if="OrderTableDataOne.trade_state==5">已撤销</el-tag>
                    <el-tag type="primary" v-if="OrderTableDataOne.trade_state==6">用户支付中</el-tag>
                    <el-tag type="primary" v-if="OrderTableDataOne.trade_state==7">转入退款</el-tag>
                    <el-tag type="primary" v-if="OrderTableDataOne.trade_state==8">支付失败</el-tag>
                </li>
                <li>创单时间：{{OrderTableDataOne.time_create | time}}</li>
                <li>支付时间：{{OrderTableDataOne.time_expire | time}}</li>
                <!--<li>附加信息：{{OrderTableDataOne.remark}}</li>-->
            </ul>
            <ul class="Store_information">
                <li>小区UUID：{{OrderTableDataOne.community_uuid}}</li>
                <li>应用名称：{{OrderTableDataOne.community_name}}</li>
                <!--<li>门店名称：{{OrderTableDataOne.shop_name}}</li>-->
                <!--<li>店铺详细地址：{{OrderTableDataOne.address}}</li>-->
            </ul>
        </el-dialog>
        <el-alert
                title="备注：若没有筛选条件，默认导出全部订单数据。"
                type="info"
                :closable="false">
        </el-alert>
        <el-button type="primary" icon="el-icon-download" style="margin:10px 0 5px 35px;" @click="Export">导出</el-button>
        <div style="float:right;margin:10px 35px;" v-show="PageDisplay">
            <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page.sync="currentPage" :page-size="page_size" layout="prev, pager, next, jumper" :total="total"></el-pagination>
        </div>
    </div>
</template>
<script>
    import {OrderList,AdminOrderView,businessSearch,backendOrderOrg,backendOrderExcel,adminOrderNotify} from '../../../api/api';
    export default {
        data() {
            return {
                times:'',//时间间隔
                CzyID:'',//彩之云ID
                MerchantID:'',//商户订单号码
                Mobile:'',//手机号码
                MerchantName:'',//商户名称
                options: [],
                list: [],
                loading: false,
                applicationName:'',//应用名称
                Order:[{
                    id:1,
                    label:"未支付"
                },{
                    id:2,
                    label:"已付款"
                },{
                    id:3,
                    label:"交易成功"
                },{
                    id:4,
                    label:"已关闭"
                },{
                    id:5,
                    label:"已撤销"
                },{
                    id:6,
                    label:"用户支付中"
                },{
                    id:7,
                    label:"转入退款"
                },{
                    id:8,
                    label:"支付失败"
                }],
                Order_status:'',//订单状态
                OrderTableData: [],//订单列表
                //详情展示数据
                details_Visible:false,//界面是否显示
                OrderTableDataOne:'',//订单详情
                Detail_data:{//展示数据
                },
                currentPage:1,//显示页数
                page_size:10,//每页数量
                total:1,//总页数
                PageDisplay:false,
                loadListing:true,//加载
                startTime:'',//开始时间
                endTime:'',//结束时间
                /////组织架构
                parent_uuid:'',
                organizational: [],
                ORG_Uuid:'',
            };
        },
        created: function () {
            this.OrderStart();
            this.Residential();
        },
        watch:{
            options:{
                handler:function(newval,oldval){console.log('...........',newval,oldval)}
            }
        },
        methods: {
            details_Click(index,row) {
                console.log(row);
                this.details_Visible = true;
                this.OrderTableDataOne='';
//                this.OrderTableDataOne=row;
                let localRoutes =JSON.parse(window.sessionStorage.getItem('user'));
                var OrderListParams ={
                    access_token:localRoutes.access_token,
                    business_uuid:row.business_uuid,
                    colour_sn:row.colour_sn
                };
                AdminOrderView(OrderListParams).then(data => {
                    if(data.code==0){
                        this.OrderTableDataOne=data.content;
                    }else{
                        this.$notify.error({
                            title: '错误提示',
                            message: data.message
                        });
                    }
                });
            },
            handleSizeChange(val) {
                console.log(`每页 ${val} 条`);
                this.currentPage=val;
                this.OrderStart();
            },
            handleCurrentChange(val) {
                console.log(`当前页: ${val}`);
                this.currentPage=val;
                this.OrderStart();
            },
            remoteMethod(query) {//搜素商户
                if (query !== '') {
                    this.loading = true;
                    let localRoutes =JSON.parse(window.sessionStorage.getItem('user'));
                    let MerchantSearch={
                        access_token:localRoutes.access_token,
                        name:query
                    };
                    businessSearch(MerchantSearch).then(data=>{
                        this.loading = false;
                        if(data.code==0){
                            this.options =data.content;
                            if(this.options.length==0){
                                this.MerchantName='';
                            }
                        }else{
                            this.$notify.error({
                                title: '错误提示',
                                message: data.message
                            });
                        }
                    });
                } else {
                    this.options = [];
                    this.MerchantName='';
                }
            },
            GetFocus(){
                this.options = [];
                this.MerchantName='';
            },
            Order_Search(){//搜索
                ///////////////////////////////////////
                let datdString=this.times[0];
                this.format(this.times[0], 'yyyy-MM-dd HH:mm:ss');
//                datdString = datdString.replace("GMT", "").replaceAll("\\(.*\\)", "");
//                //将字符串转化为date类型，格式2016-10-12
//                SimpleDateFormat format =  new SimpleDateFormat("EEE MMM dd yyyy hh:mm:ss z",Locale.ENGLISH);
//                Date dateTrans = format.parse(datdString);
//                System.out.println(new SimpleDateFormat("yyyy-MM-dd").format(dateTrans))
                ///////////////////////////////////////
                if(this.ORG_Uuid==''&&this.CzyID==''&&this.Mobile==""&&this.MerchantName==''&&this.Order_status==''&&this.times==''){
                    this.$notify({
                        title: '警告',
                        message: '请输入查询信息',
                        type: 'warning'
                    });
                }else{
                    if(this.times!=''){
                        this.startTime=this.get_unix_time(this.format(this.times[0], 'yyyy-MM-dd HH:mm:ss'));
                        this.endTime=this.get_unix_time(this.format(this.times[1], 'yyyy-MM-dd HH:mm:ss'));
                        this.OrderStart();
                    }else{
                      this.OrderStart();
                    }
//                    this.CzyID='';
//                    this.Mobile="";
//                    this.MerchantName='';
//                    this.Order_status='';
//                    this.times='';
                }
            },
            format(time,format){//转换为日期
                var t = new Date(time);
                var tf = function(i){return (i < 10 ? '0' :'') + i};
                return format.replace(/yyyy|MM|dd|HH|mm|ss/g,function(a){
                    switch(a){
                        case 'yyyy':
                            return tf(t.getFullYear());
                            break;
                        case 'MM':
                            return tf(t.getMonth() + 1);
                            break;
                        case 'mm':
                            return tf(t.getMinutes());
                            break;
                        case 'dd':
                            return tf(t.getDate());
                            break;
                        case 'HH':
                            return tf(t.getHours());
                            break;
                        case 'ss':
                            return tf(t.getSeconds());
                            break;
                    }
                })
            },
            get_unix_time(dateStr){
                let newstr = dateStr.replace(/-/g,'/');
                let date =  new Date(newstr);
                let time_str = date.getTime().toString();
                return time_str.substr(0, 10);
            },
            OrderStart(){
                let _this=this;
                let localRoutes =JSON.parse(window.sessionStorage.getItem('user'));
                let OrderListParams ={
                    access_token:localRoutes.access_token,
                    colour_sn:this.CzyID,
                    org_uuid:this.ORG_Uuid,
                    mobile:this.Mobile,
                    trade_state:this.Order_status,
                    shop_name:this.MerchantName,
                    create_time:this.startTime,
                    end_time:this.endTime,
                    page:this.currentPage,
                    page_size:this.page_size
                };
                OrderList(OrderListParams).then(data => {
                    if(data.code==0){
                        this.loadListing=false;
                        this.OrderTableData=data.content.data;
                        this.total=data.content.total;
                        if(data.content.total>10){
                            this.PageDisplay=true;
                        }else{
                            this.PageDisplay=false;
                        }
                    }else{
                        this.$notify.error({
                            title: '错误提示',
                            message: data.message
                        });
                        if(data.code=='402'){
                            _this.$router.push({ path: '/login' });
                            sessionStorage.removeItem('user');
                        }
                    }
                });

            },
            Residential(parent_uuid){
                let localRoutes =JSON.parse(window.sessionStorage.getItem('user'));
                let OrderListParams ={params:{
                    access_token:localRoutes.access_token,
                    parent_uuid:this.parent_uuid
                }
                };
                backendOrderOrg(OrderListParams).then(data => {
                    if(data.code==0){
                            let Array=[];
                            for(let i=0;i<data.content.list.length;i++){
                                let rpl={
                                    label:data.content.list[i].name,
                                    value:data.content.list[i].uuid,
                                    children:[]
                                };
                                Array.push(rpl);
                            }
                            this.organizational=Array;
                    }else{
                        this.$notify.error({
                            title: '错误提示',
                            message: data.message
                        });
                        if(data.code=='402'){
                            this.$router.push({ path: '/login' });
                            sessionStorage.removeItem('user');
                        }
                    }
                });
            },
            structure(value){//选择组织架构
                for(let i=0;i<value.length;i++){
                    this.ORG_Uuid=value[value.length-1];
                }
                if(value.length==1){
                    let localRoutes =JSON.parse(window.sessionStorage.getItem('user'));
                        let OrderListParams ={params:{
                            access_token:localRoutes.access_token,
                            parent_uuid:value[0]
                        }
                    };
                    backendOrderOrg(OrderListParams).then(data => {
                        console.log(data);
                        if(data.code==0){
                            for(let j=0;j<this.organizational.length;j++){
                                if(value[0]==this.organizational[j].value){
                                    let Array=[];
                                    for(let i=0;i<data.content.list.length;i++){
                                        let rpl={
                                            label:data.content.list[i].name,
                                            value:data.content.list[i].uuid,
                                            children:[]
                                        };
                                        Array.push(rpl);
                                    }
                                    this.organizational[j].children=Array;
                                }
                            }
                        }else{
                            this.$notify.error({
                                title: '错误提示',
                                message: data.message
                            });
                            if(data.code=='402'){
                                this.$router.push({ path: '/login' });
                                sessionStorage.removeItem('user');
                            }
                        }
                    });
                }else if(value.length==2){
                    let localRoutes =JSON.parse(window.sessionStorage.getItem('user'));
                        let OrderListParams ={params:{
                            access_token:localRoutes.access_token,
                            parent_uuid:value[1]
                        }
                    };
                    backendOrderOrg(OrderListParams).then(data => {
                        console.log(data);
                        if(data.code==0){
                            for(let j=0;j<this.organizational.length;j++){
                                if(value[0]==this.organizational[j].value){
                                    for(let p=0;p<this.organizational[j].children.length;p++){
                                        if(value[1]==this.organizational[j].children[p].value){
                                            let Array=[];
                                            for(let i=0;i<data.content.list.length;i++){
                                                let rpl={
                                                    label:data.content.list[i].name,
                                                    value:data.content.list[i].uuid,
                                                    children:[]
                                                };
                                                Array.push(rpl);
                                            }
                                            this.organizational[j].children[p].children=Array;
                                        }
                                    }
                                }
                            }
                        }else{
                            this.$notify.error({
                                title: '错误提示',
                                message: data.message
                            });
                            if(data.code=='402'){
                                this.$router.push({ path: '/login' });
                                sessionStorage.removeItem('user');
                            }
                        }
                    });
                }else if(value.length==3){
                    let localRoutes =JSON.parse(window.sessionStorage.getItem('user'));
                    let OrderListParams ={params:{
                        access_token:localRoutes.access_token,
                        parent_uuid:value[2]
                    }
                    };
//                    if(value[1]==this.organizational[j].children[p].value){
//                        let Array=[];
//                        for(let i=0;i<data.content.list.length;i++){
//                            let rpl={
//                                label:data.content.list[i].name,
//                                value:data.content.list[i].uuid
//                            };
//                            Array.push(rpl);
//                        }
//                        this.organizational[j].children[p].children=Array;
//                    }
                    backendOrderOrg(OrderListParams).then(data => {
                        console.log(data);
                        if(data.code==0){
                            for(let j=0;j<this.organizational.length;j++){
                                if(value[0]==this.organizational[j].value){
                                    for(let p=0;p<this.organizational[j].children.length;p++){
                                        if(value[1]==this.organizational[j].children[p].value){
                                            for(let m=0;m<this.organizational[j].children[p].children.length;m++){
                                                if(value[2]==this.organizational[j].children[p].children[m].value){
                                                        let Array=[];
                                                        for(let i=0;i<data.content.list.length;i++){
                                                            let rpl={
                                                                label:data.content.list[i].name,
                                                                value:data.content.list[i].uuid
                                                            };
                                                            Array.push(rpl);
                                                        }
                                                        this.organizational[j].children[p].children[m].children=Array;
                                                }
                                            }
                                        }
                                    }
                                }
                            }
                        }else{
                            this.$notify.error({
                                title: '错误提示',
                                message: data.message
                            });
                            if(data.code=='402'){
                                this.$router.push({ path: '/login' });
                                sessionStorage.removeItem('user');
                            }
                        }
                    });
                }
            },
            Export(){//导出
//                this.format(this.times[0], 'yyyy-MM-dd HH:mm:ss');
//                if(this.CzyID==''&&this.Mobile==""&&this.MerchantName==''&&this.Order_status==''&&this.times==''){
//                    this.$notify({
//                        title: '警告',
//                        message: '请输入查询信息',
//                        type: 'warning'
//                    });
//                }else{
//                    if(this.times!=''){
//                        this.startTime=this.get_unix_time(this.format(this.times[0], 'yyyy-MM-dd HH:mm:ss'));
//                        this.endTime=this.get_unix_time(this.format(this.times[1], 'yyyy-MM-dd HH:mm:ss'));
//                        this.OrderStart();
//                    }else{
//                        this.OrderStart();
//                    }
//                }
                let _this=this;
                let localRoutes =JSON.parse(window.sessionStorage.getItem('user'));
                let OrderListParams ={params:{
                        access_token:localRoutes.access_token,
                        colour_sn:this.CzyID,
                        org_uuid:this.ORG_Uuid,
                        mobile:this.Mobile,
                        trade_state:this.Order_status,
                        shop_name:this.MerchantName,
                        create_time:this.startTime,
                        end_time:this.endTime,
                        page:this.currentPage,
                        page_size:this.page_size
                    }
                };
                //正式环境
                window.location.href='http://check.account.colourlife.com/backend/order/excel?access_token='+localRoutes.access_token+'&colour_sn='+this.CzyID+'&org_uuid='+this.ORG_Uuid+'&mobile='+this.Mobile+'&trade_state='+this.Order_status+'&shop_name='+this.MerchantName+'&create_time='+this.startTime+'&end_time='+this.endTime+'&page='+this.currentPage+'&page_size='+this.page_size;
//                window.location.href='http://check.account.czytest.colourlife.com/backend/order/excel?access_token='+localRoutes.access_token+'&colour_sn='+this.CzyID+'&org_uuid='+this.ORG_Uuid+'&mobile='+this.Mobile+'&trade_state='+this.Order_status+'&shop_name='+this.MerchantName+'&create_time='+this.startTime+'&end_time='+this.endTime+'&page='+this.currentPage+'&page_size='+this.page_size;
            },
            NoticeWithdrawal(index,row){//提现通知
                var _this=this;
                console.log(row);
                this.$confirm('给【'+row.shop_name+'】发送提现通知, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    let localRoutes =JSON.parse(window.sessionStorage.getItem('user'));
                    let WthdrawsParams ={
                        access_token:localRoutes.access_token,
                        business_uuid:row.business_uuid,
                        colour_sn:row.colour_sn
                    };
                    adminOrderNotify(WthdrawsParams).then(data => {
                        console.log(data);
                        if(data.code==0){
                            this.$message({
                                type: 'success',
                                message: '发送成功!'
                            });
                        }else{
                            this.$notify.error({
                                title: '错误提示',
                                message: data.message
                            });
                            if(data.code=='402'){
//                                    _this.$router.push({ path: '/login' });
//                                    sessionStorage.removeItem('user');
                            }
                        }
                    });
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消发送'
                    });
                });
            }
        }
    };
</script>

<style lang="css">
    /*居中*/
    .cell{
        text-align: center;
    }
    li{
        list-style-type:none;
    }
    .block{
        height:auto;
        /*overflow: hidden;*/
    }
    .block li{
        float: left;
        margin:5px 5px;
    }
    .block input{
        /*width:80%;*/
    }
    .box_table{
        text-align: center;
    }
    .OneOrder li{
        height:30px;
    }
    .Store_information li{
        height:30px;
    }
</style>


